/**=====================
    File Manager
==========================**/

.file-sidebar,
.file-content {
  .card {
    .card-body,
    .card-header {
      padding: 20px !important;
      .form-group{
        input{
          line-height: 0;
        }
      }
    }
  }
}

.file-sidebar {
  ul {
    li {
      & + li {
        margin-top: 8px;
      }
    }
  }

  .btn {
    display: flex;
    align-items: center;
    padding: 8px 20px;
    font-weight: 400;
    &.btn-light {
      &:hover {
        color: var(--theme-default) !important;

        svg {
          stroke: var(--theme-default);
        }
      }
    }

    svg {
      width: 15px;
      vertical-align: middle;
      margin-right: 8px;
    }
  }

  .pricing-plan {
    border: 1px solid $light-gray;
    border-radius: 5px;
    margin-top: 10px;
    padding: 15px;
    position: relative;
    overflow: hidden;
    h6 {
      font-size: 14px;
      margin-bottom: 2px;
      color: $dark-gray;
    }
    p {
      margin-bottom: 5px;
      color: $dark-gray;
    }
    .btn {
      display: inline-block;
      font-size: 12px;
    }
    .bg-img {
      position: absolute;
      top: 40px;
      opacity: 0.1;
      transform: rotate(-45deg);
      right: -40px;
    }
  }
}

.file-manager {
  > h6 {
    opacity: 0.6;
    font-weight: 400 !important;
    font-size: 15px;
    margin-bottom: 12px;
  }
  .files {
    .file-box {
      $show-numbers: true;
      @if $show-numbers {
        @for $i from 1 through 15 {
          &:nth-child(#{$i}) {
            animation-fill-mode: both;
            animation: fadeIncustom 0.5s linear #{$i}00000ms;
          }
        }
      }
    }
    h6 {
      margin-top: 10px;
      margin-bottom: 0;
    }
  }
  p {
    font-size: 12px;
    color: $light-text;
  }
}

.files,
.folder {
  h6 {
    opacity: 0.9;
    font-weight: 500 !important;
    font-size: 14px;
  }
}

.file-content {
  .ellips {
    position: absolute;
    top: 30px;
    right: 30px;
    opacity: 0.7;
  }

  .form-inline {
    border: 1px solid $light-gray;
    padding: 0 20px;
    border-radius: 10px;
    i {
      padding-right: 10px;
      margin: auto;
      color: $dark-gray;
    }
    input {
      &::-webkit-input-placeholder {
        color: $dark-gray;
      }

      &:focus {
        outline: none !important;
      }
    }
  }

  .search-form {
    input {
      padding: 5px 10px 5px 70px;
      border-radius: 5px;
    }

    .form-group {
      &:before {
        left: 82px;
        top: 37px;
      }

      &:after {
        top: 39px;
        left: 53px;
      }
    }
  }

  .btn {
    svg {
      height: 15px;
      margin-right: 2px;
      margin-bottom: 3px;
      vertical-align: middle;
    }
  }

  h4 {
    font-weight: 500;
  }

  .folder {
    .folder-box {
      border: 1px solid $light-gray;
      border-radius: 5px;
      padding: 15px;
      background-color: $white;
      width: calc(25% - 15px);
      display: inline-block;
      margin: 2px 0;
      $show-numbers: true;
      @if $show-numbers {
        @for $i from 1 through 15 {
          &:nth-child(#{$i}) {
            animation-fill-mode: both;
            animation: fadeIncustom 0.5s linear #{$i}00000ms;
          }
        }
      }
    }
  }
  .files {
    .file-box {
      border: 1px solid $light-gray;
      border-radius: 5px;
      padding: 15px;
      background-color: $white;
      width: calc(25% - 15px);
      display: inline-block;
      position: relative;
      .file-top {
        height: 128px;
        background-color: $light;
        border: 1px solid $light-gray;
        border-radius: 5px;
        font-size: 36px;
        display: flex;
        align-items: center;
        justify-content: center;
      }
    }
  }
}

.md-sidebar {
  .md-sidebar-toggle {
    display: none;
    width: fit-content;
    margin-bottom: 20px;
    font-weight: 600;
    text-transform: capitalize;
  }
}

@media screen and (max-width: 1440px) {
  .file-content .folder .folder-box .d-flex {
    display: block;
    text-align: center;
    .flex-grow-1 {
      margin-left: 0 !important;
      margin-top: 5px;
    }
  }
}

@media screen and (max-width: 1366px) {
  .file-content {
    .files {
      margin-bottom: -10px;
      .file-box {
        width: calc(50% - 15px);
        margin-bottom: 10px;
        margin-right: 8px;
      }
    }
    .card-header {
      .btn {
        padding: 8px 15px;
      }
    }
    .folder {
      .folder-box {
        padding: 13px;
        width: calc(50% - 15px);
        margin-bottom: 10px;
        .d-flex {
          i {
            font-size: 30px;
          }
        }
      }
    }
  }
  .file-sidebar {
    .btn {
      padding: 8px 15px;
    }
  }
}

@media screen and (max-width: 768px) {
  .file-content {
    .folder {
      margin-bottom: -10px;
      .folder-box {
        width: calc(50% - 15px);
        margin-bottom: 10px;
        margin-right: 8px;
      }
    }
    .d-flex {
      display: block !important;
      text-align: center;
      .form-inline {
        .form-group {
          display: flex !important;
        }
      }
      .flex-grow-1 {
        margin-top: 10px;
        text-align: center !important;
      }
    }
  }
}
@media screen and (max-width: 420px) {
  .file-content .folder .folder-box,
  .file-content .files .file-box {
    width: calc(100%);
    margin-right: unset;
  }
  .file-content {
    h4 {
      font-size: 20px;
    }

    .card-header {
      .btn {
        padding: 7px 10px;
        font-size: 12px;
      }
    }
  }
  .file-manager {
    > h6 {
      font-size: 14px;
    }
  }
}

